import axios, { AxiosError, AxiosResponse } from 'axios';
import { toast } from 'react-toastify';
import { history } from '../..';
import { DefectFormValues, IDefect } from '../models/defect';
import { PaginatedResult } from '../models/pagination';
import { IPhoto, IProfile, IUserDefect } from '../models/profile';
import { IUser, IUserFormValues } from '../models/user';
import { store } from '../stores/store';

const sleep = (delay: number) => {
    return new Promise((resolve) => {
        setTimeout(resolve, delay)
    })
}

axios.defaults.baseURL = process.env.REACT_APP_API_URL;

axios.interceptors.request.use(config => {
    const token = store.commonStore.token;
    if (token) config.headers.Authorization = `Bearer ${token}`
    return config;
})

axios.interceptors.response.use(async response => {
    if(process.env.NODE_ENV === 'development')
    {
        await sleep(1000);
    }
    const pagination = response.headers['pagination'];
    if(pagination)
    {
        response.data = new PaginatedResult(response.data, JSON.parse(pagination));
        return response as AxiosResponse<PaginatedResult<any>>
    }
    return response;
}, (error: AxiosError) => {
    const { data, status, config } = error.response!;
    switch (status) {
        case 400:
            if (typeof data === 'string') {
                toast.error(data);
            }
            if (config.method === 'get' && data.errors.hasOwnProperty('id')) {
                history.push('/not-found');
            }
            if (data.errors) {
                const modalStateErrors = [];
                for (const key in data.errors) {
                    if (data.errors[key]) {
                        modalStateErrors.push(data.errors[key]);
                    }
                }
                throw modalStateErrors.flat()
            }
            break;
        case 401:
            toast.error('Unauthorized');
            break;
        case 404:
            toast.error("Not Found");
            history.push('/not-found');
            break;
        case 500:
            store.commonStore.setServerError(data);
            history.push('/server-error');
            break;
    }
    return Promise.reject(error);
})

const responseBody = <T>(response: AxiosResponse<T>) => response.data;

const requests = {
    get: <T>(url: string) => axios.get<T>(url).then(responseBody),
    post: <T>(url: string, body: {}) => axios.post<T>(url, body).then(responseBody),
    put: <T>(url: string, body: {}) => axios.put<T>(url, body).then(responseBody),
    del: <T>(url: string) => axios.delete<T>(url).then(responseBody),
}

const Defects = {
    list: (params: URLSearchParams) => axios.get<PaginatedResult<IDefect[]>>('/defects', {params}).then(responseBody),
    details: (id: string) => requests.get<IDefect>(`/defects/${id}`),
    create: (defect: DefectFormValues) => requests.post<void>('/defects', defect),
    update: (defect: DefectFormValues) => requests.put<void>(`/defects/${defect.id}`, defect),
    delete: (id: string) => requests.del<void>(`/defects/${id}`),
    work: (id: string) => requests.post<void>(`/defects/${id}/work`, {})
}

const Account = {
    current: () => requests.get<IUser>('/account'),
    login: (user: IUserFormValues) => requests.post<IUser>('/account/login', user),
    register: (user: IUserFormValues) => requests.post<IUser>('/account/register', user)
}

const Profiles = {
    get: (username: string) => requests.get<IProfile>(`/profiles/${username}`),
    uploadPhoto: (file: Blob) => {
        let formData = new FormData();
        formData.append('File', file);
        return axios.post<IPhoto>('photos', formData, {
            headers: { 'Content-type': 'multipart/form-data' }
        });
    },
    setMainPhoto: (id: string) => requests.post(`/photos/${id}/setMain`, {}),
    deletePhoto: (id: string) => requests.del(`/photos/${id}`),
    updateProfile: (profile: Partial<IProfile>) => { requests.put('profiles', profile) },
    updateFollowing: (username: string) => requests.post(`/follow/${username}`, {}),
    listFollowings: (username: string, predicate: string) => requests.get<IProfile[]>(`/follow/${username}?predicate=${predicate}`),
    listDefects: (username: string, predicate: string) => requests.get<IUserDefect[]>(`/profiles/${username}/defects?predicate=${predicate}`)
}

const agent = {
    Defects, Account, Profiles
}

export default agent;